body{
  width: 100%;
  height: 100vh;

}
.conteiner{
  
    display: flex;
    /* width: 80%; */
    justify-content: center;
    width: 100%;
}
.content{
  width: 80%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.hrCss{
  background-color: #f3f0f0;
  color: antiquewhite;
  right: 0px;
  left: 0px;
}